
.vue-command {
  $seashell: #f1f1f1;

  @mixin clearfix() {

    &:before,
    &:after {
      content: " ";
      display: table;
    }

    &:after {
      clear: both;
    }
  }

  overflow-y: hidden;
  overflow-x: hidden;

  .vue-command__bar {
    @include clearfix();
    position: inherit;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: #111316;
  }

  .vue-command__bar__button {
    display: inline-block;
    border-radius: 100%;

    &:before {
      content: ' ';
      display: block;
      height: 12px;
      width: 12px;
    }

    &:not(:last-child) {
      margin-right: 8px;
    }
  }

  .vue-command__bar__button--close {
    background-color: #ff5f58;
  }

  .vue-command__bar__button--minimize {
    background-color: #ffbd2e;
  }

  .vue-command__bar__button--fullscreen {
    background-color: #29ca41;
  }

  .vue-command__history {
    overflow: auto;
    word-break: break-all;
    background-color: #111316;
    display: block;
    padding: 12px 12px 12px 12px;
    margin: 0;
    white-space: pre-line;
    line-height: 1.33;
    color: $seashell;
    font-size: 1rem;
    font-family: monospace;
    color: #ffffff;
    height: 100%;

    /* Provide reasonable default values */
    ul {
      margin: 0;
      padding: 0;
      list-style-type: none;
    }

    input,
    textarea {
      background: none;
      border: none;
      outline: none;
      flex: 1;
      width: 100%;
      font-size: 1rem;
      resize: none;
      overflow: hidden;
      color: #ffffff;

      &::placeholder {
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }

  .vue-command__history__entry--fullscreen {
    height: 100%;
  }
}
